body {
  text-align: center;
}

header {
  font-size: 35px;
  padding-bottom: 3px;
}

.overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(255, 255, 255, 0.994);
}

.list {
  list-style-position: inside;
}

.button {
  margin-bottom: 10px;
}

.move {
  background-color: red;
}

.timer {
  padding-bottom: 8px;
}

